import React, { Component } from 'react';
import PropTypes from 'prop-types';
import axios from 'axios';
import Movie from './Movie';
import './App.css';

class App extends Component {
  state = {
    isLoading : true,
    movies: [],
  }
  getMovies =async() => {
    const {data : {data: {movies}}}= await axios.get('https://yts-proxy.now.sh/list_movies.json');
    // {data : {data: {movies}}} 이렇게 하면 console.log(movies로 할수있음)
    console.log(movies);
    this.setState( {
      movies: movies,
      isLoading : false,
    })
  };
  componentDidMount() {
    this.getMovies();
  }
  render() {
    const {isLoading , movies} =this.state;
    return ( 
      <section className="container">
        <div>
          {isLoading ? (
            <div className="loader"> 
              <span className="loader_text">"isLoading"</span>
            </div> 
            
            ) : (
              <div className="movies">
                {movies.map(movie => {
                console.log(movie);
                return <Movie key={movie.id} id={movie.id} year={movie.year} title={movie.title} summary={movie.summary} poster={movie.medium_cover_image}
                genres={movie.genres}  />
          } ) }
              </div>

            )}
          
        </div>
      </section>
    )
  }
}



export default App;
